.item {
  margin: 1px;
  height: 60px;
  width: 60px;
  border: 1px solid #b4b4b4;
}

.itemsArea {
  display: flex;
  flex-direction: row;
  .items {
    display: flex;
    flex-direction: column;
  }
  .itemStorage {
    height: 192px;
    width: 192px;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    .hiddenItem {
      height: 64px;
      width: 192px;
    }
  }
  .itemStorage.hidden {
    height: 192px;
    width: 192px;
  }
}
